<template>
  <div class="profile-header" @click="this.toLogin">
    <div class="header-left">
      <div class="header-img">
        <img :src="this.userInfoimgurl"  >
      </div>
      <div class="header-name">
        <div>
          <!-- <p>456</p> -->
            {{this.name}}
            <!-- wz -->
        </div>
        <span class="iconfont icon-phone" >没有绑定手机</span>
      </div>
    </div>
    <div style="margin: 0 10px">
        >
    </div>
  </div>
</template>

<script>
import  {mapState}  from 'vuex'
export default {
  name: "ProfileHeader",
  data() {
    return {
      imgBase: "//elm.cangdu.org/img/",
      name: '登录注册',
      userInfoimgurl:'data:image/jpeg;base64,'
    };
  },
//   props: ["name", "imgurl"],
  computed: {
    ...mapState({
        userInfo:state => state.user.userInfo,
    }),
  },
  methods:{
      initData() {
          if(this.userInfo){
              this.name = this.userInfo.username;
              this.userInfoimgurl= this.imgBase + this.userInfo.avatar;
          }
      },
      toLogin(){
        if(this.userInfo == null) {
          this.$router.push('login')
        }
      }
  },
  created(){
      this.initData()
      // console.log(this.userInfo)
  }
};
</script>

